export default {
	name: "",
	components: {

	},
	
	data() {
		return {
			page: 1,
			topicList: []
		}
	},
	methods: {
		// 获取专题数据
		getTopic() {
			this.$api.getTopic(this.page).then(res => {
				if (res.status === 200) {
					if(res.data.data.length>0){
						this.topicList =this.topicList.concat(res.data.data) 
						this.page++
					}else{
						uni.showToast({
							title:"暂无更多数据",
							icon:"none"
						})
					}
					
				}
			}).catch(err => {
				console.log(err)
			})
		},
		// 去专题详情页
		goDetails(id,title){
			uni.navigateTo({
				url:`/pages/specialDetails/specialDetails?id=${id}&title=${title}`
			})
		}
	},
	mounted() {

	},
	onLoad() {
		this.getTopic()
	},
	onReachBottom(){
		this.getTopic()
	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
